import React, { useState, useEffect } from 'react';
import '../../styles/style.css';

const UserProfile = () => {
  const [userData, setUserData] = useState({
    fullName: '',
    email: '',
    profileImage: null
  });
  const [isEditing, setIsEditing] = useState(false);
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [message, setMessage] = useState({ text: '', type: '' });

  useEffect(() => {
    //      
    const storedUserData = JSON.parse(localStorage.getItem('userData') || '{}');
    setUserData(storedUserData);
    setFormData({
      fullName: storedUserData.fullName || '',
      email: storedUserData.email || '',
      currentPassword: '',
      newPassword: '',
      confirmPassword: ''
    });
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleProfileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setUserData(prev => ({
          ...prev,
          profileImage: reader.result
        }));
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setMessage({ text: '', type: '' });

    //    
    const storedUserData = JSON.parse(localStorage.getItem('userData') || '{}');
    if (formData.currentPassword && formData.currentPassword !== storedUserData.password) {
      setMessage({ text: '    ', type: 'error' });
      return;
    }

    //     
    if (formData.newPassword && formData.newPassword !== formData.confirmPassword) {
      setMessage({ text: '       ', type: 'error' });
      return;
    }

    //   
    const updatedUserData = {
      ...storedUserData,
      fullName: formData.fullName,
      email: formData.email,
      profileImage: userData.profileImage
    };

    //      
    if (formData.newPassword) {
      updatedUserData.password = formData.newPassword;
    }

    //   
    localStorage.setItem('userData', JSON.stringify(updatedUserData));
    setUserData(updatedUserData);
    setIsEditing(false);
    setMessage({ text: '    ', type: 'success' });

    //     
    setFormData(prev => ({
      ...prev,
      currentPassword: '',
      newPassword: '',
      confirmPassword: ''
    }));
  };

  return (
    <div className="profile-container">
      <div className="profile-header">
        <div className="profile-image-container">
          <input 
            type="file" 
            id="profileImageUpload" 
            className="hidden" 
            accept="image/*"
            onChange={handleProfileUpload} 
          />
          <label htmlFor="profileImageUpload" className="profile-image-label">
            {userData.profileImage ? (
              <img 
                src={userData.profileImage} 
                alt=" " 
                className="profile-image" 
              />
            ) : (
              <div className="profile-image-placeholder">
                <i className="fas fa-user"></i>
              </div>
            )}
            <div className="profile-image-edit">
              <i className="fas fa-camera"></i>
            </div>
          </label>
        </div>
        <div className="profile-info">
          <h2>{userData.fullName || ' ESP'}</h2>
          <p>{userData.email || ' '}</p>
        </div>
      </div>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}

      <div className="profile-card">
        <div className="profile-card-header">
          <h3> </h3>
          <button 
            className="edit-button"
            onClick={() => setIsEditing(!isEditing)}
          >
            {isEditing ? '' : ''}
            <i className={`fas fa-${isEditing ? 'times' : 'edit'}`}></i>
          </button>
        </div>

        {isEditing ? (
          <form onSubmit={handleSubmit} className="profile-form">
            <div className="form-group">
              <label> </label>
              <div className="input-wrapper">
                <input 
                  type="text" 
                  name="fullName"
                  value={formData.fullName}
                  onChange={handleChange}
                  required
                />
                <i className="fas fa-user"></i>
              </div>
            </div>

            <div className="form-group">
              <label></label>
              <div className="input-wrapper">
                <input 
                  type="email" 
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  required
                />
                <i className="fas fa-envelope"></i>
              </div>
            </div>

            <div className="form-divider">  </div>

            <div className="form-group">
              <label>  </label>
              <div className="input-wrapper">
                <input 
                  type="password" 
                  name="currentPassword"
                  value={formData.currentPassword}
                  onChange={handleChange}
                />
                <i className="fas fa-lock"></i>
              </div>
            </div>

            <div className="form-group">
              <label>  </label>
              <div className="input-wrapper">
                <input 
                  type="password" 
                  name="newPassword"
                  value={formData.newPassword}
                  onChange={handleChange}
                />
                <i className="fas fa-key"></i>
              </div>
            </div>

            <div className="form-group">
              <label>   </label>
              <div className="input-wrapper">
                <input 
                  type="password" 
                  name="confirmPassword"
                  value={formData.confirmPassword}
                  onChange={handleChange}
                />
                <i className="fas fa-key"></i>
              </div>
            </div>

            <button type="submit" className="save-button">
               
              <i className="fas fa-save"></i>
            </button>
          </form>
        ) : (
          <div className="profile-details">
            <div className="detail-row">
              <div className="detail-label"> :</div>
              <div className="detail-value">{userData.fullName || ' '}</div>
            </div>
            <div className="detail-row">
              <div className="detail-label">:</div>
              <div className="detail-value">{userData.email || ' '}</div>
            </div>
            <div className="detail-row">
              <div className="detail-label"> :</div>
              <div className="detail-value">
                <span className="status-badge active"></span>
              </div>
            </div>
            <div className="detail-row">
              <div className="detail-label"> :</div>
              <div className="detail-value">
                {new Date().toLocaleDateString('fa-IR')}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default UserProfile;